import {mapState} from 'vuex'
export default {
	data(){
		return {
			goodsInfo:{}
		}
	},
	// 如果是路由传参
	// onLoad(options) {
	// 	this.goodsInfo = JSON.parse(options.goodsInfo) 
	// }
	
	// 如果是存储方案
	beforeMount() {
		this.goodsInfo = wx.getStorageSync('goodsInfo_key')
	},
	
	computed:{
		...mapState({
			cartList:state => state.cart.cartList
		})
	},
	
	methods:{
		addShopCart(){
			// 点击添加购物车
			// 首先要判断购物车列表当中是否存在当前这个商品相关的购物车数据
			let obj = this.cartList.find(item => item.id === this.goodsInfo.id)
			if(obj){
				// 如果有：直接修改数量
				obj.count++
				obj.isChecked = true
			}else{
				// 如果没有，需要把当前这个详情页商品数据变为购物车数据，添加到购物车列表
				obj = this.goodsInfo
				this.$set(obj,'count',1)
				this.$set(obj,'isChecked',true)
				this.cartList.push(obj)
			}
		}
	}
	
}
